import React from 'react';
import {LineTitle} from "../LineTitle";
import './style.scss';
import {Line} from "../MainTitle/GreyLine";

export const OfferMain = () => {
    return (
        <>
            <Line/>
            <div className="main_for_all_pages">
            <LineTitle title={'Оферта'}/>
                <p>Основы защиты конфиденциальности </p>
                <p>Компания Shutterstock поддерживает действующий в ЕС и США документ «Основы защиты конфиденциальности», а также аналогичный документ, действующий в Швейцарии и США, и получила соответствующий сертификат. Shutterstock делает все возможное, чтобы обращаться со всеми персональными данными, полученными из стран-членов Европейского Союза (ЕС), Великобритании и Швейцарии соответственно с соблюдением применимых принципов «Основ защиты конфиденциальности». Чтобы узнать об «Основах защиты конфиденциальности» подробнее, посетите перечень норм защиты конфиденциальности, составленный Министерством торговли США.<a rel="noreferrer" className='text_links' target={'_blank'} href={'https://www.privacyshield.gov/list'}>https://www.privacyshield.gov/list</a></p>
                <p>Shutterstock несет ответственность за обработку получаемых персональных данных в соответствии с «Основами защиты конфиденциальности» и переносит соответствующие обязательства на любых сторонних агентов, действующих по его поручению. Shutterstock соблюдает принципы защиты конфиденциальности в отношении любой дальнейшей передачи персональных данных из ЕС, Великобритании и Швейцарии, в том числе в том, что касается дальнейшего переноса обязательств.</p>
                <p>В том, что связано с персональными данными, получаемыми или передаваемыми в соответствии с «Основами защиты конфиденциальности», на Shutterstock распространяется правоприменение Федеральной торговой комиссии США. В определенных ситуациях компания Shutterstock может быть должна разгласить персональные данные в результате правомерного запроса, полученного от органов государственной власти, в том числе для обеспечения требований, связанных с национальной безопасностью и охраной правопорядка.</p>
                <p>Если у вас есть сомнения, связанные с конфиденциальностью или использованием данных, которые нам не удалось удовлетворительным образом решить, обратитесь к нашему независимому поставщику услуг по урегулированию споров в США (бесплатно) на странице <a className='text_links' rel="noreferrer" target={'_blank'} href={'https://feedback-form.truste.com/watchdog/request'}>https://feedback-form.truste.com/watchdog/request</a>.</p>
                <p>При определенных условиях, которые подробнее описываются на веб-сайте «Основ защиты конфиденциальности» <a  className='text_links' target={'_blank'} rel="noreferrer" href={'https://www.privacyshield.gov/article?id=How-to-Submit-a-Complaint'}>https://www.privacyshield.gov/article?id=How-to-Submit-a-Complaint</a>, вы вправе прибегнуть к обязывающему арбитражу, когда все прочие процедуры урегулирования споров были исчерпаны.</p>
                <p>Сбор личной информации</p>
                <p>Вы можете выполнять некоторые операции на сайте и без предоставления личной информации.</p>
                <p>Но, когда вы регистрируетесь для использования сайта Shutterstock и других продуктов или услуг и становитесь подписчиком Shutterstock, чтобы мы оказывали вам услуги, мы можем получать от вас следующую личную информацию:</p>
                <p>контактные данные (ваши имя и фамилия, номер телефона, факс и адрес электронной почты);</p>
                <p>платежные данные (номер, дата окончания срока действия и тип карты, адрес для выставления счетов);</p>
                <p>демографические сведения (должность, характер выполняемой работы, название и тип компании);</p>
                <p>данные профиля, в том числе ваши имя пользователя и пароль, сведения о ваших покупках и материалах, лицензии на которые вы оформляете у нас.</p>
                <p>Если вы отправляете материалы на сайт Shutterstock для лицензирования, вас могут попросить предоставить следующую информацию:</p>
                <p>контактные данные (ваши имя и фамилия, номер телефона, основной адрес и адрес электронной почты);</p>
                <p>Shutterstock может проверять эту информацию, попросив вас или (а) добавить отсканированную копию удостоверения личности (которая будет безопасным образом храниться на наших серверах), или (б) предоставить номер кредитной карты. Если для подтверждения личности используется номер кредитной карты, то по нему будут исключительно проверены ваши имя и адрес. Номер кредитной карты не сохраняется;</p>
                <p>платежные данные и данные автора, в том числе способ оплаты, адрес электронной почты для оплаты, желаемое отображаемое имя, тематика загружаемых материалов, используемая камера и информация об уровне ваших навыков и процессах создания материалов.</p>
                <p>В зависимости от выполняемых операций часть запрашиваемой информации обязательна к предоставлению, а часть предоставляется на ваше усмотрение. Если вы не предоставите обязательную информацию, вы не сможете выполнять соответствующие операции.</p>
                <p>Как и большинство веб-сайтов, Shutterstock и привлеченные им независимые вспомогательные операторы могут в процессе использования вами веб-сайта получать определенную техническую и маршрутную информацию о вашем компьютере, чтобы облегчить для вас использование веб-сайта и его служб. Например, мы можем записать переменные среды, такие как тип браузера, операционная система, скорость процессора и IP-адрес вашего компьютера. Мы используем эти переменные среды для отслеживания использования вами сайта и его служб, а также для того, чтобы сделать их более удобными. Shutterstock также использует такие переменные среды для отслеживания трафика на сайте. Мы можем время от времени сопоставлять эти данные с вашей личной информацией, чтобы лучше понимать потребности посетителей сайта.</p>
                <p>Для того чтобы уменьшать количество ошибок в нашей базе данных, проверять подлинность пользователей и предотвращать неправомерное использование системы, мы можем время от времени дополнять предоставленную вами личную информацию сведениями из сторонних источников. Например, для подтверждения вашей информации и во избежание ошибок в базе данных мы можем добавить к вашим контактным данным адрес, полученный от почтовой службы США. Мы можем дополнять информацию, полученную непосредственно от вас, сведениями, которые хранятся в сторонних базах данных, такими как данные о демографии или о компаниях, для того чтобы повысить вероятность того, что любые отправляемые вам маркетинговые сообщения будут действительно полезны и интересны вам. Список сторонних вспомогательных операторов, к услугам которых мы прибегаем для обработки информации, приводится на этой странице.</p>
                <p>Когда вы загружаете и используете наши мобильные приложения или службы, мы автоматически получаем информацию о типе используемого вами устройства, а также версии операционной системы. Мы ни в коем случае не запрашиваем никакой информации о местоположении с вашего мобильного устройства при загрузке или использовании вами наших мобильных приложений или служб, не получаем доступа к такой информации и не отслеживаем ее.</p>
                <p>Обновление личной информации и настроек конфиденциальности</p>
                <p>По запросу Shutterstock предоставит вам сведения о вашей личной информации, которую мы обрабатываем, и доступ к ней. Чтобы запросить эту информацию, обратитесь к нам по адресу privacy@shutterstock.com.</p>
                <p>У вас есть право в любое время получать доступ к своей личной информации и настройкам конфиденциальности, чтобы изменить или удалить их. Это можно сделать в разделе Мой аккаунт, где вы сможете незамедлительно просмотреть и изменить большую часть своей личной информации. Если вы хотите удалить свой аккаунт или отправить нам запрос, чтобы мы больше не использовали вашу информацию для оказания вам услуг, откройте раздел Настройки аккаунта на сайте и выполните инструкции в разделе «Удалить аккаунт». Если вы являетесь автором, то изменить свой профиль можете на этой странице. В целях безопасности некоторые элементы личной информации можно изменить, только обратившись в службу поддержки. Мы оперативно ответим на ваш запрос в разумные сроки.</p>
            </div>

        </>
    );
};

